#caro-banner {
  position: relative;
  background-image: url(http://abhtheme.com/html-preview/tm/dekha/dekha/images/banner.jpg?fbclid=IwAR2X1kiAv1QjNBCiTbRO-PfijGIbYo7bU82kUSaOHP9CGYBQQ6Nx5vKYTgc);
  background-size: cover;
